html {
  width: 100%; }

body {
  width: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  font-style: inherit;
  text-align: left;
  vertical-align: baseline;
  font-family: tahoma,arial,'Hiragino Sans GB',sans-serif; }

* {
  letter-spacing: 0.4px; }

a {
  text-decoration: none; }
  a:hover, a:focus, a:active {
    text-decoration: none; }

input {
  outline: none; }
  input:hover, input:focus, input:active {
    outline: none; }

textarea {
  outline: none; }
  textarea:hover, textarea:focus, textarea:active {
    outline: none; }

.container {
  *zoom: 1; }
  .container:after {
    content: '';
    display: table;
    line-height: 0;
    clear: both; }

.container {
  box-sizing: border-box;
  padding-right: 10px;
  padding-left: 10px;
  margin-right: auto;
  margin-left: auto;
  position: relative; }

@media (min-width: 768px) {
  .container {
    width: 766px; } }

@media (min-width: 992px) {
  .container {
    width: 990px; } }

button {
  cursor: pointer; }

.footer {
  position: relative; }
  .footer__top {
    padding: 37px 0;
    border-bottom: 1px solid #ccc; }
    @media (max-width: 740px) {
      .footer__top {
        padding: 30px 0; } }
    @media (max-width: 690px) {
      .footer__top {
        padding-bottom: 0;
        border-bottom: none; } }
    .footer__top .container {
      display: flex;
      flex-direction: column;
      align-items: center; }
      @media (max-width: 690px) {
        .footer__top .container {
          padding: 0; } }
    .footer__top .footer__social {
      position: absolute;
      top: -20px; }
      @media (max-width: 650px) {
        .footer__top .footer__social {
          display: none; } }
      [dir='rtl'] .footer__top .footer__social {
        left: 10px; }
      [dir='ltr'] .footer__top .footer__social {
        right: 10px; }
  .footer__form {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: center; }
  .footer__form-title {
    margin: 8px auto 27px;
    width: 100%;
    max-width: 495px;
    font-size: 18px;
    font-weight: 500;
    color: #2c2c2c;
    letter-spacing: 0.4px;
    line-height: 26px;
    text-align: center; }
    @media (max-width: 992px) {
      .footer__form-title {
        margin-top: 20px; } }
    @media (max-width: 650px) {
      .footer__form-title {
        margin: 0 auto 16px;
        max-width: 300px;
        font-size: 15px;
        letter-spacing: 0.33px;
        line-height: 26px; } }
  .footer__form-input {
    display: flex; }
    @media (max-width: 650px) {
      .footer__form-input {
        flex-direction: column;
        align-items: center; } }
  .footer__form-btn {
    width: 172px;
    height: 46px;
    color: #fff;
    background-color: #f04c53;
    border: none; }
    [dir='rtl'] .footer__form-btn {
      margin-right: 8px;
      border-radius: 25px 0 0 25px; }
      @media (max-width: 650px) {
        [dir='rtl'] .footer__form-btn {
          margin: 0;
          width: 100%;
          border-radius: 25px; } }
      @media (max-width: 420px) {
        [dir='rtl'] .footer__form-btn {
          width: 290px; } }
    [dir='ltr'] .footer__form-btn {
      margin-left: 8px;
      border-radius: 0 25px 25px 0; }
      @media (max-width: 650px) {
        [dir='ltr'] .footer__form-btn {
          margin: 0;
          width: 100%;
          border-radius: 25px; } }
      @media (max-width: 420px) {
        [dir='ltr'] .footer__form-btn {
          width: 290px; } }
  .footer__form-field {
    padding: 0 26px;
    box-sizing: border-box;
    width: 380px;
    height: 46px;
    font-size: 14px;
    border: 1px solid #ccc; }
    @media (max-width: 650px) {
      .footer__form-field {
        text-align: center; } }
    @media (max-width: 420px) {
      .footer__form-field {
        width: 290px; } }
    .footer__form-field::placeholder {
      color: #868686; }
    [dir='ltr'] .footer__form-field {
      border-radius: 25px 0 0 25px; }
      @media (max-width: 650px) {
        [dir='ltr'] .footer__form-field {
          margin: 0 0 15px;
          border-radius: 25px; } }
    [dir='rtl'] .footer__form-field {
      border-radius: 0 25px 25px 0; }
      @media (max-width: 650px) {
        [dir='rtl'] .footer__form-field {
          margin: 0 0 15px;
          border-radius: 25px; } }
  .footer__docs {
    margin: 0 0 15px;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    list-style-type: none; }
    @media (max-width: 420px) {
      .footer__docs {
        margin-bottom: 20px; } }
  .footer__docs-item {
    display: block;
    margin: 0 15px;
    font-size: 14px;
    text-decoration: underline;
    color: #2196f3; }
    @media (max-width: 992px) {
      [dir='ltr'] .footer__docs-item {
        margin-bottom: 18px; } }
    @media (max-width: 650px) {
      [dir='rtl'] .footer__docs-item {
        margin-bottom: 18px; } }
  .footer__services {
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    list-style-type: none; }
    @media (max-width: 690px) {
      [dir='rtl'] .footer__services {
        text-align: right; } }
    @media (max-width: 650px) {
      [dir='ltr'] .footer__services {
        text-align: left; } }
    @media (max-width: 690px) {
      .footer__services {
        flex-wrap: nowrap;
        flex-direction: column;
        border-bottom: 1px solid #b0b0b0; } }
  .footer__services-item {
    display: block;
    margin: 0 12px;
    font-size: 12px;
    text-decoration: underline;
    letter-spacing: 0.1px;
    color: #2c2c2c; }
    @media (max-width: 992px) {
      [dir='ltr'] .footer__services-item {
        margin-bottom: 18px; } }
    @media (max-width: 690px) {
      [dir='ltr'] .footer__services-item {
        margin-bottom: 0; } }
    @media (max-width: 690px) {
      .footer__services-item {
        margin: 0;
        padding: 0 15px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        width: 100%;
        height: 35px;
        font-size: 11px;
        background-color: #ededed;
        border-top: 1px solid #b0b0b0; } }
  .footer__bottom {
    padding-top: 13px;
    padding-bottom: 20px; }
    .footer__bottom .container {
      display: flex;
      align-items: center; }
      @media (max-width: 650px) {
        .footer__bottom .container {
          justify-content: space-between;
          flex-wrap: wrap; } }
      [dir='rtl'] .footer__bottom .container {
        flex-direction: row-reverse; }
      [dir='ltr'] .footer__bottom .container {
