    display: flex; }
  .footer__payments-item {
    margin: 0 3px;
    width: 28px;
    height: 20px;
    border-radius: 3px; }
    .footer__payments-item svg {
      width: 100%;
      height: 100%; }
  .footer__comodo {
    width: 190px;
    height: 40px; }
    @media (max-width: 650px) {
      .footer__comodo {
        display: none; } }
    [dir='rtl'] .footer__comodo {
      margin-right: 35px; }
    [dir='ltr'] .footer__comodo {
      margin-left: 35px; }
    .footer__comodo img {
      width: 100%;
      height: 100%;
      object-fit: contain; }
  .footer__social {
    display: flex; }
  .footer__social-link {
    margin: 0 7px;
    overflow: hidden;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    flex-shrink: 0; }
    @media (max-width: 420px) {
      .footer__social-link {
        margin: 0 5px;
        width: 30px;
        height: 30px; } }
    .footer__social-link svg {
      width: 100%;
      height: 100%; }
  .footer__contacts {
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: right; }
    @media (max-width: 650px) {
      .footer__contacts {
        margin-top: 18px; } }
    [dir='rtl'] .footer__contacts {
      text-align: right; }
    [dir='ltr'] .footer__contacts {
      text-align: left; }
  .footer__contacts-row {
    margin-top: 6px;
    font-size: 12px;
    color: #2c2c2c; }
    @media (max-width: 420px) {
      .footer__contacts-row {
        margin-top: 3px;
        font-size: 10px; } }
